<script>
import Icon, { types } from "@/components/Icon/index.vue";
import ListInline from "@/components/listInline/index.vue";
import Sidebar from "@/components/sidebar/index.vue";
import Avatar from "@/components/Avatar/index.vue";
import http from "@/utils/request.js";
export default {
  components: {
    Icon,
    ListInline,
    Sidebar,
    Avatar,
  },
  data() {
    return {
      cart: [],
      publicPath: process.env.BASE_URL,
      products: [],
      types,
      token: localStorage.getItem("token"),
    };
  },
  mounted() {
    this.products = [
      {
        id: 1,
        name: "商品1",
        price: 108,
        priceBeforeDiscount: 208,
        img: this.publicPath + "img/p1.jpg",
        count: 1,
      },
      {
        id: 2,
        name: "商品2",
        price: 108,
        priceBeforeDiscount: 208,
        img: this.publicPath + "img/p2.jpg",
        count: 1,
      },
      {
        id: 3,
        name: "商品3",
        price: 108,
        priceBeforeDiscount: 208,
        img: this.publicPath + "img/p3.jpg",
        count: 1,
      },
      {
        id: 4,
        name: "商品4",
        price: 108,
        priceBeforeDiscount: 208,
        img: this.publicPath + "img/p4.jpg",
        count: 1,
      },
      {
        id: 5,
        name: "商品5",
        price: 108,
        priceBeforeDiscount: 208,
        img: this.publicPath + "img/p5.jpg",
        count: 1,
      },
      {
        id: 6,
        name: "商品6",
        price: 108,
        priceBeforeDiscount: 208,
        img: this.publicPath + "img/p6.jpg",
        count: 1,
      },
      {
        id: 7,
        name: "商品7",
        price: 108,
        priceBeforeDiscount: 208,
        img: this.publicPath + "img/p7.jpg",
        count: 1,
      },
      {
        id: 8,
        name: "商品8",
        price: 108,
        priceBeforeDiscount: 208,
        img: this.publicPath + "img/p8.jpg",
        count: 1,
      },
      {
        id: 9,
        name: "商品9",
        price: 108,
        priceBeforeDiscount: 208,
        img: this.publicPath + "img/p9.jpg",
        count: 1,
      },
    ];
    // 获取vuex中的cart数据
    this.cart = this.$store.state.cart;
  },
  methods: {
    goCart() {
      this.$store.commit("updateCart", this.cart);
      this.$router.push("/cart");
    },
    addCart(item) {
      const index = this.cart
        .map((item) => item.id)
        .findIndex((i) => i === item.id);
      console.log("index");
      if (index > -1) {
        this.cart[index]["count"]++;
      } else {
        this.cart.push(item);
      }
      this.$message({
        message: "加购成功",
        type: "success",
      });
      this.$store.commit("updateCart", this.cart);
    },
    login() {
      console.log("=== login ===");
      this.$router.push("/login");
    },
    logout() {
      console.log("=== logout ===");
      this.$confirm("确定要退出登录吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        http
          .post("/api/user/account/logout")
          .then(() => {
            localStorage.clear();
            this.$router.push("/login");
          })
          .catch((err) => {
            console.log("err:", err);
          });
      });
    },
    chat() {
      console.log("=== chat ===");
      this.$router.push("/chat");
    },
  },
};
</script>

